
body {
  margin: 20px;
  background-color: #f0f0f0;
}
h1 {
  font: bold 26px/1 Helvetica, Arial, sans-serif;
  text-transform: uppercase;
}
.tradegraph {
  width: 100%;
  height: 1000px;
  background-color: white;
}
.node__edge {
  stroke-width: 1px;
  opacity: .3;
}
.node__text {
  font: bold 14px/1 Helvetica, Arial sans-serif;
  text-transform: uppercase;
  fill: #676767;
}
.node--firms .node__edge {
  stroke: #ff6557;
}
.node--consumers .node__circle {
  fill: #33ccff;
}
.node--consumers .node__edge {
  stroke: #33ccff;
}
.node--out {
  fill-opacity: .1;
  stroke-opacity: .1;
}
.node.branch {
  cursor: pointer;
}
.node.branch .node__circle {
    stroke-width: 2px;
}
.node.branch.node--firms .node__circle {
    stroke: #ff3624;
}
.node.branch.node--consumers .node__circle {
    stroke: #00ace6;
}
.link {
  fill: none;
  stroke: rgba(0, 0, 0, 0.3);
  -webkit-animation-iteration-count: infinite;
          animation-iteration-count: infinite;
}
.link:hover {
    stroke: #97e582;
    cursor: pointer;
}
.link:nth-child(1) {
    -webkit-animation-delay: 0.2s;
            animation-delay: 0.2s;
}
.link:nth-child(2) {
    -webkit-animation-delay: 0.5s;
            animation-delay: 0.5s;
}
.link:nth-child(3) {
    -webkit-animation-delay: 0.6s;
            animation-delay: 0.6s;
}
.link:nth-child(4) {
    -webkit-animation-delay: 1s;
            animation-delay: 1s;
}
.link:nth-child(5) {
    -webkit-animation-delay: 0.4s;
            animation-delay: 0.4s;
}
.link:nth-child(6) {
    -webkit-animation-delay: 2.2s;
            animation-delay: 2.2s;
}
.link:nth-child(7) {
    -webkit-animation-delay: 1.2s;
            animation-delay: 1.2s;
}
.link:nth-child(8) {
    -webkit-animation-delay: 0.1s;
            animation-delay: 0.1s;
}
.link:nth-child(9) {
    -webkit-animation-delay: 3s;
            animation-delay: 3s;
}
.link:nth-child(10) {
    -webkit-animation-delay: 2.6s;
            animation-delay: 2.6s;
}
.link:nth-child(11) {
    -webkit-animation-delay: 2s;
            animation-delay: 2s;
}
.link:nth-child(12) {
    -webkit-animation-delay: 1.6s;
            animation-delay: 1.6s;
}
.link:nth-child(13) {
    -webkit-animation-delay: 0.4s;
            animation-delay: 0.4s;
}
.link:nth-child(14) {
    -webkit-animation-delay: 0.5s;
            animation-delay: 0.5s;
}
.link:nth-child(15) {
    -webkit-animation-delay: 0.1s;
            animation-delay: 0.1s;
}
.link:nth-child(16) {
    -webkit-animation-delay: 1.9s;
            animation-delay: 1.9s;
}
.marker {
  fill: rgba(0, 0, 0, 0.3);
  stroke-width: 2px;
}
@-webkit-keyframes pulsate {
0% {
    opacity: 1;
}
30% {
    opacity: .3;
}
100% {
    opacity: 1;
}
}
@keyframes pulsate {
0% {
    opacity: 1;
}
30% {
    opacity: .3;
}
100% {
    opacity: 1;
}
}



